.mainBlockWrapper {
    height: 100%;
    overflow: hidden;
    padding: 30px;
    position: relative;
    color: #fff;
    background: linear-gradient(45deg, #3e526a 0%,#072142 100%);
}

.mainBlockWrapper .mainBlockContainer {
    padding: 0;
    position: relative;
    height: 100%;
}



.mainBlockContainer {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

