.leftBlockWrapper {
    z-index: 10;
    height: 100%;
    background: cadetblue;
}

.rightBlockWrapper {
    z-index: 10;
    height: 100%;
    background: darkred;
}

.text {
    width: 300px;
    background: darkcyan;
}

.photo {
    width: 300px;
    height: 400px;
    background: darkcyan;
}

